<template>
    <div class="return">
        <div class="bread_crumb">
            <!--<span>位置：主页 | </span>-->
            <span>当前位置：</span>
            <span>快速归还</span><br>
        </div>
        <div class="people">个人信息</div>
        <div class="Information">
            <div class="Left">
                <div class="LeftImg"></div>
                <el-input v-model="teacherNumber" class="p_search" type="password" placeholder="请刷卡识别"
                          @keyup.enter.native="checkTeacherNumber()"></el-input>
            </div>
            <div class="ipt">
                <el-form :label-position="labelPosition" label-width="80px" :model="teacherForm">
                    <el-form-item label="姓名">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="性别">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.sex_type"></el-input>
                    </el-form-item>
                    <el-form-item label="教师编号">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.id_number"></el-input>
                    </el-form-item>
                    <el-form-item label="职位">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.position"></el-input>
                    </el-form-item>
                    <el-form-item label="联系方式">
                        <el-input :readonly="is_read" unselectable="on" v-model="teacherForm.call"></el-input>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="people">未归还物品列表</div>
        <div class="apply">
            <div class="Search">
                <div class="search_input el-input el-input--suffix">
                    <input autocomplete="off" v-model="returnSearch" placeholder="请输入要搜索的物品名称" type="text" rows="2"
                           validateevent="true"
                           suffixicon="el-icon-search" class="p_search el-input__inner" @keyup.enter.active="searchNotClick()">
                    <span class="el-input__suffix" @click="searchNotClick()">
                        <span class="el-input__suffix-inner">
                            <i class="el-input__icon iconfont el-icon-pharmaceutical_administration-sousuo"></i>
                        </span>
                    </span>
                </div>
            </div>
            <!--<el-button class="connect" @click="connectionClick()" type="primary" style="">连接设备</el-button>-->
        </div>
        <!-- 模态框 -->
        <el-button :style="teacherForm.id_number ? '': 'display:none;'" class="left_btn" type="primary" @click="connectionClick()">归还</el-button>
        <div class="ModelUse" v-if="showModel">
            <div class="Model">
                <div class="ModelT">
                    <span>归还物品</span>
                    <i class="el-icon-close" @click="closeBox()"></i>
                </div>
                <div class="ModelMain">
                    <div class="ModelTop">归还物品扫描结果</div>
                    <div class="table_main">
                        <el-table ref="multipleTable" :data="scanningData3" stripe
                                  tooltip-effect="dark" style="width: 100%"
                                  @selection-change="handleSelectionChange"
                                  :row-class-name="tableRowClassName">
                            <el-table-column type="selection" width="55"></el-table-column>
                            <el-table-column prop="item_id" label="物品编号" width="100"></el-table-column>
                            <el-table-column prop="item_name" label="物品名称"></el-table-column>
                            <el-table-column prop="pic" label="物品图片" width="120">
                                <template slot-scope="scope">
                                    <img v-if="scope.row.photo" :src="$store.state.operationPhotoPath + scope.row.photo" style="width: 71px;height: 30px;">
                                    <span v-if="!scope.row.photo">无</span>
                                </template>
                            </el-table-column>
                            <el-table-column prop="sort_name" label="分类"></el-table-column>
                            <el-table-column prop="usage" label="剩余量">
                                <template slot-scope="scope">
                                    <el-input :class="scope.row.is_beyond == 2 ? 'is_beyond' : ''"
                                              type="text" v-model="scope.row.usage" @keyup.native="scope.row.is_beyond =1">
                                    </el-input>
                                </template>
                            </el-table-column>
                            <el-table-column label="是否损坏" width="100">
                                <template slot-scope="scope">
                                    <el-select v-model="scope.row.is_damage" placeholder="">
                                        <el-option label="是" value="0" key="0"></el-option>
                                        <el-option label="否" value="1" key="1"></el-option>
                                    </el-select>
                                </template>
                            </el-table-column>
                            <el-table-column prop="remarks" label="备注" width="200">
                                <template slot-scope="scope">
                                    <el-input type="text" v-model="scope.row.remarks"></el-input>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>
                    <div class="ModelBtm">扫描结果：合计{{ scanningData3.length }}件物品，<span style="color: #f05050;">{{ notMy }}</span>件物品不在归还列表范围，明细请参照上面列表。</div>
                    <el-button class="Models_btn" type="primary" @click="confirmBox()">确认扫描结果无误</el-button>
                </div>
            </div>
        </div>
        <!--列表数据-->
        <div class="table_list">
            <el-table ref="NotTable" :data="myNotList" stripe tooltip-effect="dark" style="width: 100%">
                <el-table-column type="index" label="序号" width="150"></el-table-column>
                <el-table-column prop="item_id" label="物品编号"></el-table-column>
                <el-table-column prop="item_name" label="物品名称" show-overflow-tooltip></el-table-column>
                <el-table-column prop="receive_at" label="领用时间" show-overflow-tooltip></el-table-column>
                <el-table-column prop="overdue_at" label="逾期时间" show-overflow-tooltip></el-table-column>
                <el-table-column label="状态">
                    <template slot-scope="scope">
                        <span :style="scope.row.status_at > 0 ? 'color: #f05050;' : ''">
                            {{ scope.row.status_at | over_status}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column label="是否逾期">
                    <template slot-scope="scope">
                        <span>{{ scope.row.status_at | is_over }}</span>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="block" style="text-align: center;margin-top: 30px;">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="pageIndex"
                    :page-size="4"
                    layout="total, prev, pager, next, jumper"
                    :total="pageTotal">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                teacherNumber: '',
                is_read: true,
                labelPosition: "right",
                teacherForm: {},
                myNotList: [],          //我未归还的物品列表(分页显示)
                myAllNot: [],           //我所有未归还的物品（归还扫描时扫描对比）
                returnSearch: "",       //input搜索值
                showModel: false,
                pageIndex: 1,           //第几页
                pageTotal: 0,
                scanningData3: [],      //扫描到的数据
                scanningSelection: [],  //扫描到的数据多选中的
                notMy: 0,
                //webSocket
                ws:'',
                timer: '',                      //启动websocket连接
            };
        },
        created() {
            this.$store.state.sideIndex = -1;
        },
        mounted(){
        },
        beforeDestroy() {
            if(this.ws){
                this.ws.close();
            }
            if(this.timer) { //如果定时器还在运行 或者直接关闭，不用判断
                clearInterval(this.timer); //关闭
            }
        },
        methods: {
            //刷卡
            checkTeacherNumber() {
                if(this.teacherNumber == ''){
                    this.$message({
                        message: '请刷卡！',
                        type: 'warning',
                        duration: '1000',
                    });
                } else if(this.teacherNumber == 0){
                    this.$message({
                        message: '该卡无教师信息！',
                        type: 'warning',
                        duration: '1500',
                    });
                    this.teacherNumber = '';
                } else {
                    this.$axios.post('/user-list', {
                        number: 999999999,
                        id_number: this.teacherNumber
                    }).then((response) => {
//                    console.log(response)
                        if (response.data.data.data == undefined) {
                            //该卡无教师信息
                            this.$message({
                                message: '该卡无教师信息！',
                                type: 'warning',
                                duration: '1500',
                            });
                            this.teacherForm = {};
                            this.myReceivedList = [];
                        } else {
                            this.teacherForm = response.data.data.data[0];
                            if (this.teacherForm.sex == 1) {
                                this.teacherForm.sex_type = "男";
                            } else {
                                this.teacherForm.sex_type = "女";
                            }
                            this.checkMyList();
                            this.getMyAllNot();
                        }
                        this.teacherNumber = '';
                    }).catch((err) => {})
                }
            },
            //查看我未归还的物品列表
            checkMyList() {
                this.$axios.post('/receive', {
                    id_number: this.teacherForm.id_number,
                    number: 4,
                    page: 1,
                }).then((response) => {
//                    console.log(response);
                    this.myNotList = response.data.data.data;
                    if (response.data.data.data.length == 0) {
                        this.pageTotal = 0;
                    } else {
                        this.pageTotal = response.data.data.total;
                    }
                }).catch((error) => {})
            },
            //获取我所有未归还的物品列表
            getMyAllNot(){
                this.$axios.post('/receive', {
                    id_number: this.teacherForm.id_number,
                    number: 9999999999,
                    page: 1,
                }).then((response) => {
                    console.log(response);
                    this.myAllNot = response.data.data.data;
                }).catch((error) => {
                })
            },
            //搜索
            searchNotClick() {
                this.$axios.post('/receive', {
                    id_number: this.teacherForm.id_number,
                    number: 4,
                    page: 1,
                    item_name: this.returnSearch
                }).then((response) => {
//                    console.log(response);
                    this.myNotList = response.data.data.data;
                    if (response.data.data.data.length == 0) {
                        this.pageTotal = 0;
                    } else {
                        this.pageTotal = response.data.data.total;
                    }
                }).catch((error) => {})
            },
            //分页
            handleCurrentChange(val) {
                this.$axios.post('/receive', {
                    id_number: this.teacherForm.id_number,
                    number: 4,
                    page: val,
                    item_name: this.returnSearch,
                }).then((response) => {
//				console.log(response);
                    this.myNotList = response.data.data.data;
                    if (response.data.data.data.length == 0) {
                        this.pageTotal = 0;
                    } else {
                        this.pageTotal = response.data.data.total;
                    }
                }).catch((error) => {
                })
            },

            //连接设备（点击归还）
            connectionClick(){
                let self = this;
                this.ws = new WebSocket('ws://127.0.0.1/tag-pool');
                this.ws.onopen = function (evt) {
                    self.showModel = true;
                    //重置
                    //获取扫描到的物品列表
                    self.scanningData3 = [];
                    //计算不是我的物品的数量
                    self.notMy = 0;
                };
                let arr = [];
                this.ws.onmessage = function (event) {
                    let cardArr = [];
                    var data = event.data;
//                    console.log(data)
                    if(JSON.parse(data).length == 0){
                        //获取扫描到的物品列表
                        self.scanningData3 = [];
                        //计算不是我的物品的数量
                        self.notMy = 0;
                    }
                    if(arr.sort().toString() == JSON.parse(data).sort().toString()){
                    } else {
                        arr = JSON.parse(data);
                        for(let m in arr){
                            cardArr.push(arr[m].epc)
                        }
//                        //获取扫描到的物品列表
//                        self.scanningData3 = [];
//                        //计算不是我的物品的数量
//                        self.notMy = 0;
                        if(cardArr.length == 0){
                        } else {
                            self.$axios.post('/frid-sel',{
                                frid_list: cardArr
                            }).then((response) => {
//                                console.log("扫描到的数据");
//                                console.log(response.data.data);
                                if(response.data.message == "无数据"){
                                    //有未绑定的标签 导致无数据返回
                                    self.$message({
                                        type: 'warning',
                                        message: '请勿扫描未绑定的标签!',
                                        duration: '2000',
                                    });
                                } else {
                                    //获取扫描到的物品列表
                                    self.scanningData3 = [];
                                    //计算不是我的物品的数量
                                    self.notMy = 0;
                                    //扫描到的数据
                                    for (let x in response.data.data) {
                                        if (response.data.data[x][0].photo == null) {
                                            self.scanningData3.push({
                                                item_name: response.data.data[x][0].item_name,
                                                item_id: response.data.data[x][0].item_id,
                                                frid_number: response.data.data[x][0].frid_number,
                                                sort_name: response.data.data[x][0].sort_name,
                                                sort_id: response.data.data[x][0].sort_id,
                                                photo: null,
                                                usage: '',
                                                is_damage: '',
                                                remarks: '',
                                                amount: response.data.data[x][0].amount,
                                                is_beyond: 1,   //剩余量填写是否超出 1 否 2 超出
                                            })
                                        } else {
                                            self.scanningData3.push({
                                                item_name: response.data.data[x][0].item_name,
                                                item_id: response.data.data[x][0].item_id,
                                                frid_number: response.data.data[x][0].frid_number,
                                                sort_name: response.data.data[x][0].sort_name,
                                                sort_id: response.data.data[x][0].sort_id,
                                                photo: response.data.data[x][0].photo.split('/')[1],
                                                usage: '',
                                                is_damage: '',
                                                remarks: '',
                                                amount: response.data.data[x][0].amount,
                                                is_beyond: 1,   //剩余量填写是否超出 1 否 2 超出
                                            })
                                        }
                                    }
                                    //扫描到的数据和我未归还的数据比较  区分是我的  和 不是我未归还的
                                    for (let x in self.scanningData3) {
                                        for (let y in self.myAllNot) {
                                            if (self.scanningData3[x].frid_number == self.myAllNot[y].frid_number) {
                                                self.scanningData3[x].isMy = 1; //是我的
                                                //判断我未归还的是否已经逾期
                                                if (self.myAllNot[y].status_at > 0) {
                                                    self.scanningData3[x].is_overdue = 0;
                                                } else {
                                                    self.scanningData3[x].is_overdue = 1;
                                                }
                                                break;
                                            } else {
                                                self.scanningData3[x].isMy = 2;
                                            }
                                        }
                                    }
                                    for (let i in self.scanningData3) {
                                        if (self.scanningData3[i].isMy == 1) {
                                        } else {
                                            self.notMy++;
                                        }
                                    }
                                    console.log(self.scanningData3)
                                }
                            }).catch((err) => {})
                        }
                    }
                };
                this.ws.onclose = function (evt) {};
                this.ws.onerror = function(event) {
//                    console.log(event)
                    self.$message({
                        message: '请先连接硬件并开启软件！',
                        type: 'warning',
                        duration: '2000',
                    });
                    self.ws = '';
                };
            },
            //关闭弹框
            closeBox() {
                this.showModel = false;
                //重置
                //获取扫描到的物品列表
                this.scanningData3 = [];
                //计算不是我的物品的数量
                this.notMy = 0;
            },
            tableRowClassName({row, rowIndex}) {
//                console.log({row, rowIndex})
//                console.log(row.isMy)
                if(row.isMy == 1){
                    //是我未归还的物品
                } else {
                    return 'warning-more';
                }
            },
            //归还物品扫描数据的多选
            handleSelectionChange(val) {
//                console.log(val);
                this.scanningSelection = val;
            },
            //确认扫描无误
            confirmBox() {
                console.log(this.scanningData3)
                console.log(this.scanningSelection)
                if(this.scanningSelection.length == 0){
                    this.$message({
                        message: '请先选择最终要归还的物品！',
                        type: 'warning',
                        duration: '1500',
                    });
                } else {
                    let flag = true;        //判断是否都已经填写
                    let allMy = true;       //判断是否都是我的未归还物品
                    let normal = true;      //填写的剩余量是否正常（是否为数字）
                    let over = true;      //填写的剩余量是否超出（没有超出原本存量）
                    for(let i in this.scanningSelection){
//                        console.log(this.scanningSelection[i].isMy)
                        if(this.scanningSelection[i].isMy == 2){
//                            console.log("不是我的")
                            allMy = false;
                            break;
                        } else {
//                          console.log("我的都是我的")
                        }
                    }
                    //检查是否有没有填写的数据
                    for(let i in this.scanningSelection){
                        if(this.scanningSelection[i].usage == ''||this.scanningSelection[i].is_damage == ''||this.scanningSelection[i].remarks == ''){
                            flag = false;
                            break;
                        } else {
                            if(isNaN(this.scanningSelection[i].usage)){
                                this.scanningSelection[i].is_beyond = 2;
                                normal = false;
//                                break;
                            } else {
                                if(Number(this.scanningSelection[i].usage) <= Number(this.scanningSelection[i].amount)){
                                } else {
                                    this.scanningSelection[i].is_beyond = 2;
                                    over = false;
//                                    break;
                                }
                            }
                        }
                    }
                    if(allMy){  //都是我的
                        if(flag){   //都填写
                            if(normal){ //是数字
                                if(over){//没超出
                                    let user_id = this.teacherForm.id;
                                    let site_use = '无';
                                    let recipient = JSON.parse(localStorage.getItem("user")).name; //经办人

                                    let arr = this.scanningSelection;
                                    for(let s in arr){
                                        arr[s].user_id = this.teacherForm.id;
                                        arr[s].site_use = '无';
                                        arr[s].recipient = JSON.parse(localStorage.getItem("user")).name; //经办人
                                    }
                                    this.$axios.post('/restore-edit', {
                                        restore: arr
                                    }).then((response) => {
//                                        console.log(response);
                                        if(response.data.status == '0'){
                                            this.$message({
                                                message: '归还成功！',
                                                type: 'success',
                                                duration: '1500',
                                            });
                                            this.showModel = false;
                                            this.checkMyList();//查看我未归还的列表
                                            this.getMyAllNot();
                                            this.scanningSelection = [];
                                            this.scanningData3 = [];
                                        }
                                    }).catch((error) => {})
                                } else {
                                    this.$message({
                                        message: '填写的剩余量已超出库存量！',
                                        type: 'warning',
                                        duration: '1500',
                                    });
                                }
                            } else {
                                this.$message({
                                    message: '填写的剩余量应为数字值！',
                                    type: 'warning',
                                    duration: '1500',
                                });
                            }
                        } else {
                            this.$message({
                                message: '请填写相关数据！',
                                type: 'warning',
                                duration: '1000',
                            });
                        }
                    } else {
                        this.$message({
                            message: '只能归还本人未归还列表中的物品！',
                            type: 'warning',
                            duration: '1500',
                        });
                    }
                }
            }
        },
        filters: {
            over_status: function (value) {
                if (value > 0) {
                    return "逾期" + value + "天";
                } else if (value == 0) {
                    return "今天到期";
                } else {
                    return Math.abs(value) + "天后到期";
                }
            },
            is_over: function (value) {
                if (value <= 0) {
                    return "否";
                } else {
                    return "是";
                }
            }
        }
    };
</script>

<style lang="less" rel="stylesheet/less">
    .return {
        background-color: #ffffff;
        padding: 17px 24px 30px 24px;
        margin: 0 10px 0 10px;
        overflow: hidden;
        //不是我未归还的物品
        .el-table .warning-more {
            color: #f05050;
        }
        //填写的剩余量超出原本存量
        .is_beyond {
            .el-input__inner{
                 color: #f00;
            }
        }
        .bread_crumb {
            height: 22px;
            font-family: PingFangSC;
            font-size: 16px;
            text-align: left;
            color: #223241;
            p {
                display: inline-block;
            }
        }
        .people {
            margin-top: 22px;
            padding-bottom: 5px;
            border-bottom: 1px solid #d4d4d5;
        }
        .Information {
            margin-top: 18px;
            width: 595px;
            height: 239px;
            overflow: hidden;
            border-radius: 10px;
            background-color: #f5f8fb;
            border: 1px dashed #5ca8e7;
            padding-top: 10px;
            .Left {
                margin-left: 34px;
                margin-top: 16px;
                margin-right: 60px;
                float: left;
                .LeftImg {
                    width: 114px;
                    height: 142px;
                    border: 1px solid black;
                }
                .el-input {
                    width: 120px;
                    height: 30px;
                    border-radius: 22px;
                    cursor: pointer;
                    color: white;
                    outline: none;
                    margin-top: 21px;
                }
            }
            .ipt {
                float: left;
                display: block;
                .el-input {
                    left: 30px;
                }
                .el-form-item {
                    margin: 0;
                }
                .el-input__inner {
                    height: 25px;
                    line-height: 25px;
                    width: 239px;
                    border: none;
                }
            }
        }
        .apply {
            margin-top: 16px;
            display: inline-block;
            .block {
                display: inline-block;
                span {
                    font-size: 14px;
                }
                .el-input__inner {
                    height: 30px;
                    width: 162px;
                }
                .el-input__prefix i {
                    line-height: 30px;
                    cursor: pointer;
                    width: 26px;
                    height: 30px;
                    color: white;
                    border-radius: 0 4px 4px 0px;
                    background-color: #5ca8e7;
                    position: relative;
                    left: 130px;
                }
            }
            //连接设备
            .connect {
                display: inline-block;
                height: 30px;
                line-height: 30px;
                padding: 0 12px;
                background-color: #5abfac;
                border-color: #5abfac;
                border-radius: 5px;
            }
            .Search {
                display: inline-block;
                margin-right: 30px;
                .el-input.search_input {
                    width: 366px;
                    height: 30px;
                    border-radius: 13px;
                    margin-left: 20px;
                    .el-input__inner {
                        background-color: #f1f1f3;
                        height: 30px;
                        line-height: 30px;
                        border-radius: 20px;
                        border: 0;
                    }
                    .el-input__icon {
                        line-height: 30px;
                    }
                    .el-input__suffix {
                        i {
                            &:before {
                                border-radius: 20px;
                                display: block;
                                width: 25px;
                                height: 25px;
                                line-height: 25px;
                                font-size: 16px;
                                background-color: #5ca8e7;
                                color: #ffffff;
                                text-align: center;
                                margin-top: 2.5px;
                                font-weight: bold;
                                cursor: pointer;
                            }
                        }
                    }
                }
            }
        }
        .left_btn.el-button--primary {
            height: 30px;
            border-radius: 5px;
            background-color: #5abfac;
            border-color: #5abfac;
            padding: 8px 20px;
            margin-left: 20px;
        }
        .el-table th,
        .el-table tr:nth-child(0) {
            background-color: #223241;
            margin-top: 16px;
        }
        .el-table__footer-wrapper,
        .el-table__header-wrapper {
            margin-top: 16px;
        }
        .el-checkbox__inner {
            border-radius: 50%;
        }
        .ModelUse {
            position: fixed;
            top: 0;
            left: 0;
            z-index: 333;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.15);
            overflow: auto;
        }
        .Model {
            /*width: 784.1px;*/
            width: 900px;
            height: 643.3px;
            object-fit: contain;
            background-color: #ffffff;
            box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.5);
            position: absolute;
            top: 193px;
            left: 50%;
            margin-left: -392px;
            .ModelT {
                height: 60px;
                background-color: #5ca8e7;
                text-align: center;
                color: white;
                span {
                    line-height: 60px;
                    font-size: 20px;
                    display: inline-block;
                }
                i {
                    font-size: 27px;
                    font-weight: 600;
                    cursor: pointer;
                    position: absolute;
                    right: 15px;
                    top: 15px;
                }
            }
            .ModelMain {
                margin-top: 20px;
                text-align: center;
                height: 100%;
                .table_main {
                    width: 850px;
                    height: 415px;
                    margin: 0 auto;
                    .el-table__body-wrapper {
                        width: 848px;
                        background-color: #ffffff;
                        height: 336px;
                        overflow-y: auto;
                        max-height: 336px;
                        table-layout: auto;
                        border: 1px solid #979797;
                        .el-table__body{
                            width: 848px!important;
                            margin-left: 1px;
                        }
                    }
                    .el-table--fit{
                        border-bottom: 1px solid #979797;
                    }
                }
                .el-table__body, .el-table__footer, .el-table__header {
                    table-layout: auto;
                }
                .el-table th > .cell {
                    color: white;
                    text-align: center;
                }
                .ModelBtm {
                    text-align: left;
                    padding-left: 50px;
                }
                .Models_btn {
                    margin-top: 25px;
                    height: 30px;
                    border-radius: 5px;
                    background-color: #5abfac;
                    border-color: #5abfac;
                    padding: 8px 20px;
                }
            }
        }
        .table_list {
            //序号
            .has-gutter {
                tr {
                    th:first-child {
                        padding-left: 50px;
                    }
                }
            }
            tbody {
                tr {
                    td:first-child {
                        padding-left: 50px;
                    }
                }
            }
        }
    }
</style>


